.card-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.card {
    width: 18rem;
    margin: 10px;
}

.card-body {
    cursor: pointer;
}

.copied {
    display: none;
}

.copy-tooltip {
    display: block;
    
}

button {
    margin: 0 10px;
}